/**
 * user 
 */

import base from './base'; // 
import axios from '@/plugins/axios'; // httpaxios
import qs from 'qs'; // qs


const image = {
    // 
    getImageDetails(params) {
        return axios.get(`${base.pro}/image/getImageDetails`, {
            params: params
        })
    },
    // 
    getImageList(params) {
        return axios.get(`${base.pro}/image/getImageList`, {
            params: params
        })
    },
    //
    getCommentsByiid(params) {
        return axios.get(`${base.pro}/Comment/getCommentsByiid`, {
            params: params
        })
    },
    // 
    getImageListByUid(params) {
        return axios.get(`${base.pro}/image/getImageListByUid`, {
            params: params
        })
    },
    // 
    addComment(params) {
        return axios.post(`${base.pro}/Comment/add`, qs.stringify(params))
    },
    // 
    setStar(params) {
        return axios.get(`${base.pro}/image/isStar`, {
            params: params
        })
    },
    // 
    setThumb(params) {
        return axios.get(`${base.pro}/image/isThumb`, {
            params: params
        })
    },
    // 
    myStars(params) {
        return axios.get(`${base.pro}/image/myStars`, {
            params: params
        })
    },
    //  
    report(params) {
        return axios.post(`${base.pro}/Comment/report`, qs.stringify(params))
    },
    //   
    getListByTagName(params) {
        return axios.get(`${base.pro}/image/getListByTagName`, {
            params: params
        })
    },
    // 
    updateTitle(params) {
        return axios.post(`${base.pro}/image/updateTitle`, qs.stringify(params))
    },
    // 
    getListBySearch(params) {
        return axios.get(`${base.pro}/image/getListBySearch`, {
            params: params
        })
    }
}

export default image;