/**
 * user 
 */

import base from './base'; // 
import axios from '@/plugins/axios'; // httpaxios
import qs from 'qs'; // qs

const admin = {
    // 
    getUserList(params) {
        return axios.get(`${base.pro}/admin/getUserList`, {
            params: params
        })
    },
    manageStatus(params) {
        return axios.post(`${base.pro}/admin/manageCountStatus`, params, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
    },
    createCount(params) {
        return axios.post(`${base.pro}/admin/createCount`, qs.stringify(params))
    },
    // 
    getReportComment(params) {
        return axios.get(`${base.pro}/Comment/getReportComment`, {
            params: params
        })
    },
    // 
    dealWithReport(params) {
        return axios.post(`${base.pro}/admin/dealWithReport`, qs.stringify(params))
    }
}

export default admin;