import base from './base'; // 



const root = {
    // 
    getUrl(url) {
        return base.pro + '/image' + url.slice(28)
    },
    //  url
    getOriginalUrl(url) {
        var header = url.slice(28)
        header = header.split("/")
        header[3] = "original"
        header = header.join("/")
        return base.pro + '/image' + header
    }
}

export default root;